"use client";

import CertificateCard from "./CertificateCard"; // Importa la tarjeta de certificado
import certificatesData from "../../data/certificatesData"; // Importa los datos de certificados
import Image from "next/image";
import { useTheme } from "../../hooks/useTheme"; // Hook para el tema

export default function CertificatesPage() {
  const theme = useTheme(); // 游댳 Obtiene el tema actual

  return (
    <div className="p-6 relative">
      {/* 游댳 Imagen de la esquina superior derecha */}
      <Image
        src={theme === "dark" ? "/assets/dark/certifications.svg" : "/assets/light/certifications.svg"}
        alt="Certificaciones"
        width={100}
        height={100}
        priority // Optimiza la carga
        className="absolute top-0 right-0"
      />

      {/* 游댳 T칤tulo principal */}
      <h1 className="text-2xl font-bold text-center mb-6">Tus Certificaciones</h1>

      {/* 游댳 Contenedor de certificados */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {certificatesData.map((certificate) => (
          <CertificateCard key={certificate.id} {...certificate} />
        ))}
      </div>
    </div>
  );
}
